(function() {
    'use strict';


    /* Controllers */

    angular.module('websiteApp').controller('loginController', loginController);

    loginController.$inject = ['Logger', 'User'];

    function loginController(Logger, User) {

        /* jshint validthis: true */
        var vm = this;

        /**********  Data Binding For CSS style   **********/
        vm.css = {
            currentDeleteIcon : -1,
            currentSelected : -1,
            loginFormErrorMessage : 0

        };


        /**********  Data Binding For ViewModel  **********/
        vm.data = {
            newUser : {
                username : '',
                password : ''
            }
        };


        /**********  Event Center  **********/
        vm.event = {
            login : login,
            hideArticleDeleteIcon : hideArticleDeleteIcon,
            clickArticle : clickArticle
        };


        function login(form){

            if (form.$invalid) {
                return ;
            }

            User.login(vm.data.newUser).then(function(data){
                console.log(data);
            }).catch(function(err){
                if (err && err.data && err.data.code){
                    vm.css.loginFormErrorMessage = err.data.code;
                }
                Logger.errorXHR(err);
            });

        }

        function hideArticleDeleteIcon(){

            vm.css.currentDeleteIcon = -1;

        }

        function clickArticle(currentIndex){

            vm.css.currentSelected = currentIndex;

        }




        /**********  App Init  **********/

        function appInit (){

        }

        function appReRun(){

        }

        appInit();

    }



})();


(function() {
  'use strict';


  /* Services Model */


// Demonstrate how to register services
// In this case it is a simple value service.

  angular.module('websiteApp').factory('Logger', loggerService);

  loggerService.$inject = ['$log'];

  function loggerService($log) {
      return {
          debug: debug,
          errorXHR: errorXHR
      };

      function errorXHR(err) {
          $log.error('XHR Failed. ' );
          $log.debug(err.data );
      }

      function debug(info) {
          $log.debug('Debug Info : ' + info );
          return info;
      }

  }
})();


(function() {
  'use strict';


  /* Services Model */


// Demonstrate how to register services
// In this case it is a simple value service.

  angular.module('websiteApp').factory('User', userService);

  userService.$inject = ['$http'];

  function userService($http) {
      return {
          login: login
      };

      function login(user) {
          return $http.post('/api/user/login', user)
              .then(getUserLogin);
            //   .catch(Logger.errorXHR);

          function getUserLogin(response) {
              return response.data;
          }
      }
  }
})();



(function() {
    'use strict';


    /* Directives */


    angular.module('websiteApp').directive('editMenu', function () {
        return {
            restrict: 'EA',
            templateUrl: 'tpl_demo10_edit.html',
            scope: {
                menulist : '=datalistmodel',
                menu : '=datamodel',
                index : '=index'
            },

            link: function (scope, element, attrs) {
                scope.cssshowmenu = true;
                scope.editMenuButton = function(){
                    scope.cssshowmenu = false;
                };

                scope.saveEditMenuButton = function(){
                    scope.cssshowmenu = true;
                };
                scope.deleteMenuButton = function(){
                    scope.cssshowmenu = true;

                    for(var i = scope.menulist.length-1; i >= 0; i--){
                        if (scope.menulist[i].id == scope.menu.id) {
//                            console.log(i, scope.menulist[i].id, scope.menu.id, scope.menulist);
                            scope.menulist.splice(i, 1);
                            element.remove();
                            break;
                        }
                    }
                };


            }
        };
    });


    angular.module('angularDemoApp').directive('addMenu', function () {
        return {
            restrict: 'EA',
            templateUrl: 'tpl_demo10_add.html',
            scope: false,
            link: function (scope, elem, attrs) {
                scope.maxmenu = Number(attrs.maxmemu);
                console.log(Number(attrs.maxmemu));
                scope.newmemu = {id:100, name:"" };
                scope.cssshowmenu = true;
                scope.cssshowbox = true;

                if(scope.menulist.length >= scope.maxmenu){
                    scope.cssshowbox = false;
                }

                scope.addMenuButton = function(){
                    scope.cssshowmenu = false;
                };

                scope.saveNewMenuButton = function(){
                    scope.cssshowmenu = true;
                    scope.menulist.push(angular.copy(scope.newmemu));
                    if(scope.menulist.length >= scope.maxmenu){
                        scope.cssshowbox = false;
                    }
                };

            }
        };
    });

})();



(function() {
    'use strict';


    /* Directives */

    /* recommended */
    /* demo14.directive.js */

    /**
     * @desc order directive that is specific to the order module at a company named Acme
     * @example <div my-nav-bar></div>
     */


    angular.module('websiteApp').directive('myNavBar', [myNavBarComponent]);


    function myNavBarComponent() {

        var directive = {
            restrict: 'EA',
            templateUrl: '/angulardemo/js/component/demo/demo14_tpl.html',
            scope: {
                menulist : '='
            },

            link: linkFunc
        };

        return directive;


        function linkFunc (scope, element, attrs) {
            scope.css = {
                currentEditMenuIndex : -1,
                currentSelectMenuIndex : -1,
                currentHoverMenuIndex : -1,
                showAddNewMenuForm : false
            };
            scope.data = {
                newMenu : {
                    id : scope.menulist[scope.menulist.length-1].id + 1,
                    name : ''
                }
            };


            scope.addMenu = function(){
                scope.css.showAddNewMenuForm = true;
            };
            scope.saveNewMenu = function(){
                scope.data.newMenu.id =scope.menulist[scope.menulist.length-1].id + 1;
                scope.menulist.push(angular.copy(scope.data.newMenu));
                scope.css.showAddNewMenuForm = false;
            };


            scope.editMenu = function(index){
                scope.css.currentEditMenuIndex = index;

            };
            scope.saveEditMenu = function(){
                scope.css.currentEditMenuIndex = -1;
            };


            scope.deleteMenu = function(index){

                scope.menulist.splice(index, 1);
                scope.css.currentEditMenuIndex = -1;

            };
            scope.deleteMenu2 = function(menu){

                for(var i = scope.menulist.length-1; i >= 0; i--){
                    if (scope.menulist[i].id === menu.id) {
                        scope.menulist.splice(i, 1);
                        break;
                    }
                }

                scope.css.currentEditMenuIndex = -1;
            };

        }

    }



})();
